<template>
  <div id="app">
    <van-popup v-model="videoShow" @click-overlay="closeed()">
        <video v-for="video in videoList" :key="video.name" :poster="video.videoImg" :src="video.videoSrc" autoplay="autoplay" height="100%" width="100%" type="video/mp4">
            your browser does not support the video tag
        </video>
    </van-popup>
    <van-pull-refresh v-model="loading" @refresh="getVideoList(false)">
      <van-list
        v-model="loading"
        :finished="finished"
        @load="getVideoList(true)"
      >
        <img v-for="img in list" :key="img.name" :src="img.pic_url" class="picshow" @click="onVideoToPopup(img.pic_url, img.video_url)"/>
      </van-list>
    </van-pull-refresh>    
  </div>
</template>

<script>
import { List, PullRefresh, Popup } from 'vant';
import ApiPath from '../config/apiPath';
import Utils from '../utils/Utils';

export default {
  components: {
    [List.name]: List,
    [Popup.name]: Popup,
    [PullRefresh.name]: PullRefresh
  },
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      videoList: [],
      videoShow: false
    };
  },
  methods: {
    getVideoList(isWaterfall) {
      this.finished = true;
      this.$http.get(ApiPath.video).then(result => {
        this.loading = false;
        if (result.body.code === 0) {
          if (isWaterfall) {
            result.body.data.forEach((item, index) => {
              this.list.push(item);
            });
          } else {
            this.list = result.body.data;
          }
          var _this = this;
          setTimeout(function() {
            _this.finished = false;
          }, 500);
        }
      });
    },
    onVideoToPopup(picUrl, videoUrl) {
      var url = '/video/' + Utils.encodeBase64Content(picUrl) + '/' + Utils.encodeBase64Content(videoUrl);
      this.$router.push({ path: url });
    },
    closeed() {
      this.$router.push({ path: '/' });
    },
    videoMethods() {
      if (this.$route.params.pic) {
        var videoSrc = ApiPath.video + '/' + this.$route.params.video;
        var picSrc = Utils.decodeBase64Content(this.$route.params.pic);
        this.videoList.push({ 'name': videoSrc, 'videoImg': picSrc, 'videoSrc': videoSrc });
        this.videoShow = true;
      } else {
        this.videoList.splice(0, this.videoList.length);// 
        this.videoShow = false;
      }
    }
  },
  mounted() {
    this.videoMethods();
  },
  // ,
  watch: {
    '$route': 'videoMethods'
  }
};
</script>

<style scoped>

.van-cell__title .van-icon {
  font-size: 18px;
}
.picshow {
  width: 49.8%;
  vertical-align:text-top;
  margin-bottom: 1px; 
}
.picshow.picshow:nth-child(1) {
  margin-right: 1px; 
}
.van-popup {
    width: 90%;
    height: 90%;
}
</style>



